body,
html {
  margin: 0;
  padding: 0;
  background-color: rgba(255, 235, 205, 0.5);
}

.App-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nav {
  z-index: 10;
  position: fixed;
  height: 10vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.link {
  color: rgba(0, 0, 0, 0.75);
  font-size: 1.25vw;
  cursor: pointer;
  font-family: "Roboto";
}

#contacte {
  font-family: "Roboto";
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(212, 212, 212);
  width: 100%;
  color: rgba(0, 0, 0, 0.75);
  height: 15vh;
}
